      tarsiusCardExpanded: false,
      tarsiusDialog: false,
      tamiasDialog: false,
      isurusDialog: false,
      variousDialog: false,
      githubDialog: false,
      assetsDialog: false
    }
  },
  methods: {
    toggleDialog (dialogName, dialogState) {
      this[dialogName] = dialogState
      window.dispatchEvent(new Event('resize'))
    },
    openGallery (lightboxRef, index) {
      this.$refs[lightboxRef].showImage(index)
    }
  }
}
</script>

<style scoped>

.gallery-img:hover {
  cursor: pointer;
}

.card-overlay-title {
  background-color: rgba(0, 0, 0, 0.5)
}

</style>



